'use server';

import { GoogleGenerativeAI } from '@google/generative-ai';

export async function generateManagementPlan(formData: FormData) {
  // ▼▼▼ 診断用ログ出力 ▼▼▼
  const apiKey = process.env.GEMINI_API_KEY;
  console.log("--- 診断開始 ---");
  if (!apiKey) {
    console.error("❌ エラー: APIキーが読み込まれていません。");
    console.error("原因: .env.local のファイル名が間違っているか、場所が違います。");
    throw new Error("システムエラー: APIキーの設定が見つかりません（.env.localを確認してください）");
  } else {
    // セキュリティのため、キーの最初の4文字だけ表示して確認します
    console.log(`✅ APIキーを認識しました: ${apiKey.substring(0, 4)}...`);
  }
  // ▲▲▲ 診断終了 ▲▲▲

  const genAI = new GoogleGenerativeAI(apiKey);
  
  // 入力項目の取得
  const industry = formData.get('industry') as string || '（未入力）';
  const businessDetail = formData.get('businessDetail') as string || '（未入力）';
  const hqLocation = formData.get('hqLocation') as string || '（未入力）';
  const otherLocations = formData.get('otherLocations') as string || 'なし';
  const businessArea = formData.get('businessArea') as string || '（未入力）';
  const employeeCount = formData.get('employeeCount') as string || '（未入力）';
  const futureEmployeeCount = formData.get('futureEmployeeCount') as string || 'なし';
  const strengths = formData.get('strengths') as string || '（未入力）';
  const issues = formData.get('issues') as string || '（未入力）';
  const newInitiatives = formData.get('newInitiatives') as string || '（未入力）';
  const targetCustomers = formData.get('targetCustomers') as string || '（未入力）';
  const acquisitionChannel = formData.get('acquisitionChannel') as string || '（未入力）';
  const vision = formData.get('vision') as string || '（未入力）';

  // モデル設定（最も標準的な名前を使用）
  const model = genAI.getGenerativeModel({ model: 'gemini-1.5-flash' });

  const prompt = `
あなたは経営コンサルタントです。以下の情報から経営計画書の「第1章から第8章（施策50個含む）」を作成してください。
業種: ${industry}
事業内容: ${businessDetail}
強み: ${strengths}
課題: ${issues}
想い: ${vision}
(以下省略せずに詳細に出力してください)
`;

  try {
    const result = await model.generateContent(prompt);
    const response = await result.response;
    return response.text();
  } catch (error: any) {
    // 詳しいエラーをターミナルに表示
    console.error("--- Gemini API エラー詳細 ---");
    console.error(error);
    console.error("---------------------------");
    throw new Error(`AIエラー: ${error.message || "詳細不明"}`);
  }
}